.admin-navbar-p8 {
    background-color:black;
    padding: 10px;
    color: white;
    height: 40px;
    align-items: center;
    justify-content: center;
    padding-top: 25px;
    font-size: 18px;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
   
}
 
.navbar-content-p8 {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
 
.menu-buttons-p8 {
    display: flex;
}
 
.menu-button-p8 {
    margin-left: 55px; /* Increased margin for extra gap */
    color: white;
    text-decoration: none;
    background: none; /* Remove button background */
    border: none; /* Remove button border */
    cursor: pointer; /* Change cursor on hover */
    transition: color 0.3s; /* Smooth transition for color change */
}
 

 
.user-name-p8 {
    font-size: 18px;
    font-weight: 600;
}
 
.profile-icon-p8 {
    position: relative; /* Position relative for dropdown placement */
    cursor: pointer; /* Change cursor to pointer */
    margin-left: 45px; /* Spacing from other buttons */
    margin-right: 10px;
}
 
.dropdown-p8 {
    position: absolute; /* Position dropdown below the icon */
    background-color: black;
    border-radius: 8px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    right: 0; /* Align dropdown to the right */
    color: white;
    z-index: 10; /* Ensure it appears above other elements */
    display: none; /* Initially hidden */
}
 
.profile-icon-p8:hover .dropdown-p8 {
    display: block; /* Show dropdown on hover */
}
 
.dropdown-item-p8 {
    padding: 10px 15px; /* Padding for dropdown items */
    cursor: pointer; /* Change cursor on hover */
    color: white; /* Text color */
}
 
.dropdown-item-p8:hover {
    background-color: #f1f1f1; /* Change background on hover */
    color: #640D5F; /* Change text color on hover */
}

.menu-button-p8 {
    color: whitesmoke; /* Default color */
    text-decoration: none;
    font-family: Arial, sans-serif; /* Default font */
}
 
.menu-button-p8.active {
    
    font-weight:100; /* Make it bold to stand out */
   
    transform:scale(1.3);
    color:#6EC207; /* Change color for active item */
}

/* Add this CSS for the sidebar */
.sidebar-p8 {
    position: fixed;
    top: 0;
    left: -250px; /* Start hidden off-screen */
    width: 250px;
    height: 100%;
    background-color: black;
    color: white;
    transition: left 0.3s ease; /* Smooth transition */
    z-index: 1000; /* Ensure it's above other elements */
}

.sidebar-open-p8 {
    left: 0; /* Slide in */
}

.sidebar-content-p8 {
    padding: 20px;
}

.sidebar-item-p8 {
    display: block;
    color: white;
    text-decoration: none;
    margin: 10px 0;
}

.sidebar-item-p8:hover {
    color: #640D5F; /* Change color on hover */
}

.menu-toggle-p8 {
    cursor: pointer;
    margin-left: auto; /* Aligns the menu toggle to the right */
    font-size: 24px; /* Size for the hamburger icon */
}



@media (max-width: 768px) {
    .navbar-content-p8 {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .menu-buttons-p8 {
        display: none; /* Hide regular menu buttons */
    }

    .sidebar-p8 {
        padding-top: 60px; /* Space for the navbar */
    }
}

@media (min-width: 769px) {
    .sidebar-p8 {
        display: none; /* Hide sidebar on larger screens */
    }
}